@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700&family=Roboto:wght@400;500;700&display=swap');
@keyframes scrollDownBtnAni {
  0%{
    margin-bottom: 0;
  }
  10%{
    margin-bottom: -8px;
  }  
  20%{
    margin-bottom: 0px;
  }
  30%{
    margin-bottom: -4px;
  }
  40%{
    margin-bottom: 0px;
  }
  50%{
    margin-bottom: -2px;
  }
  60%{
    margin-bottom: 0px;
  }
  75%{
    margin-bottom: 0px;
  }
  100%{
    margin-bottom: 0px;
  }
}
body{
  font-family: 'Noto Sans KR', sans-serif;
  font-family: 'Roboto', sans-serif;
  color: #222;    
  overflow-x:hidden !important;
}

header{
  width: 100%;
  height: 54px;
  position: fixed;
  z-index: 9;
 .df{
   position: relative;
   display: flex;
   box-sizing: border-box;
   box-sizing: border-box;
   padding: 0px 0px 0px 48px;
   justify-content: space-between;
   z-index: 9;
   h1 a{
      background: url(../public/img/Logo.png)0px 50%/100% auto no-repeat;
      display: block;
      width: 120px;      
      height: 54px;      
      text-indent: -9999px;         
   }
   .centerMenuItem{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
   }
   ul{     
     display: block;               
     z-index: 9;     
     li{
       float: left; 
       padding-right: 24px;
       z-index: 9;
       a{
         line-height: 54px;
         z-index: 12;
         font-weight: 500;
       }
     }
     li.pr0{
       padding-right: 0px;
     }
   }   
 }
 .mobile{
  display: block;
  position: fixed;
  left: 0;
  width: 100%;
  
  
  .menuTab{    
    top: 0;
    bottom: 0;
    right: 0;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 311px;      
    height: 100vh;
    background: #FFF;            
    position: absolute;
    display: block;
    z-index: 42;      
    transition: all 1s;          
  }
  .menuTab.off{
    margin-right: -311px;
    transition: all 0.3s;      
  }
  .menuTab.on{
    margin-right: 0px;
    transition: all 0.5s;      
  }
  .menuBg{      
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: block;
    width: 100%;
    height: 100vh;
    z-index: 1;      
    background: rgba(0,0,0,0.3);
    backdrop-filter: blur(4px);      
  }
  .menuBg.off{
    transition: opacity 0.3s;
    display: none;
    opacity: 0;
  }
  .menuBg.on{
    display: block;
    transition: opacity 0.5s;      
    opacity: 1;
  }
  .closeBtn{
    cursor: pointer;
    position: fixed;
    right: 8px;
    font-size: 1.4rem;
    color: #555;
    margin-top: 12px;
    padding: 8px 14px;
    border-radius:14px;
    border: none;
    background: none;
  }
  .closeBtn.off{
    display: none;
  }
  .closeBtn.on{
    display: block;
  }
  ul{      
    padding: 60px 0px 60px 40px;      
  }
  li{
    width: 100%;
    a{
      line-height: 48px !important;
    }
    a.lh0{
      line-height: 24px !important;
      p{
        display: inline-block;
      }
      i{
        vertical-align: top;
      }
      span{
        padding: 0px;
        font-size: 0.9rem;        
      }
    }      
  }
  li:last-child{
    padding-bottom: 60px;
  }
}
 .cursorFollow{
   position: absolute;
   line-height: 2rem;
   background: rgba(0,0,0,0.1);        
   color: rgba(0,0,0,0.1);        
   border-radius: 10px;      
   z-index: -1;
   padding: 0px 8px !important;   
   transition: all 0.3s ease;
   top: 50%;
   transform: translate(-8px,-50%);
 } 
 .cursorFollow.on{
   opacity: 1;
   transition: all 0.1s linear;
 }
 .cursorFollow.off{
  opacity: 0;
  transition: all 0.1s linear;
}

}
/*page common*/
.page{
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%,50%;
  width: 100%;
  height: 100vh;
  position: relative;
}
.btns{
  display: flex;
  position: absolute;
  bottom: 114px;
  left: 50%;
  transform: translateX(-50%);
  a{
    padding: 12px 0px;
    display: block;
    float: left;
    text-align: center;
    width: 256px;
    margin: 0px 12px;
    border-radius: 20px;
    font-size: 0.8rem;    
  }
  a.left{
    background: rgba(23, 26, 32, 0.8);
    color: #FFF;
  }
  a.right{
    background: rgba(255,255,255,0.8);
    color: #222;
  }
}
.top{
  position: absolute;
  left: 50%;
  top: 15%;
  transform: translate(-50%,0%);
  h2{
    text-align: center;
    font-size: 2.5rem;
  }
  a{
    display: inline-block;
    position: relative;
  }
  a span{
    position: absolute;
    display: block;
    width: 100%;
    background: #000;
    height: 1px;
    bottom: -1px;
    transition: all 0.5s;
  }
  a:hover span{
    position: absolute;
    display: block;
    width: 100%;
    background: #000;
    height: 2px;
    bottom: -2px;
    transition: all 0.5s;
  }
}
.page1{
  background-image: url('../public/img/homeBG_1.jpg');
  .scrollDownBtn{  
    position: absolute;
    background: none;
    border: none;
    bottom: 20px;
    left: 50%;
    font-size: 1.5rem;    
    cursor: pointer;
    transform: translateX(-50%);
    color: #444;    
    animation: scrollDownBtnAni 2s 0s infinite linear;
  }
}
.page2{
  background-image: url('../public/img/homeBG_2.jpg')
}
.page3{
  background-image: url('../public/img/homeBG_3.jpg')
}
.page4{
  background-image: url('../public/img/homeBG_4.jpg')
}
.page5{
  background-image: url('../public/img/homeBG_5.jpg')
}
.page6{
  background-image: url('../public/img/homeBG_6.jpg')
}
.page7{
  background-image: url('../public/img/homeBG_7.jpg')
}
/* footer */
footer{
  position: absolute;
  display: flex;
  bottom: 24px;
  left: 50%;
  padding: 0px;
  transform: translateX(-50%);
  a{    
    font-size: 0.8rem;
    margin: 0px 9px;
  }
}
/* mobile */
@media screen and (max-width:599px) {
  .page1{
    background-image: url('../public/img/homeBG_1_mobile.jpg');
  }
  .page2{
    background-image: url('../public/img/homeBG_2_mobile.jpg');
  }
  .page3{
    background-image: url('../public/img/homeBG_3_mobile.jpg');
  }
  .page4{
    background-image: url('../public/img/homeBG_4_mobile.jpg');
  }
  .page5{
    background-image: url('../public/img/homeBG_5_mobile.jpg');
  }
  .page6{
    background-image: url('../public/img/homeBG_6_mobile.jpg');
  }  
  .page{
    background-position:50%;
    background-size: cover 100%;
  }

  .mobile_dn{
    display: none;
  }
  .menuBtn button{
    cursor: pointer;
    position: fixed;
    right: 8px;
    font-size: 1rem;
    margin-top: 12px;
    padding: 8px 14px;
    border-radius:14px;
    border: none;
    background: rgba(0,0,0,0.1);
  }
  .page .top{
    width: 100%;
    text-align: center;
  }
  .pc{
    display: none;
  }
  .btns{    
    display: block;
    bottom: 92px;
    width: 90%;
    a{      
      width: 100%;
      margin: 8px 0px !important;
    }    
  }
  
  /* footer */
  .mup{
    bottom: 184px;
  }
  footer{
    position: absolute;    
    bottom: 24px;
    left: 50%;
    padding: 0px;
    transform: translateX(-50%);
    display: block;
    a{
      display: block !important;
      width: 100% !important;
      font-size: 0.8rem;     
      padding: 12px 0px !important; 
      margin: 0 0 !important;
      text-align: center;
    }
    a.mobile_dn{
      display: none !important;
    }
  }
}
